@import '../common/styles/sass/variables';
@import '../common/styles/sass/mixins';

.mainBlock {
    min-height: 80vh;
    @include flex(row, center, center, nowrap);
    padding: 100px 0;
    .particles{
        position: absolute;
        z-index: 0;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        opacity: 0.3;
    }
    .container {
        @include container();
        @include flex(row, space-around, center, wrap);
        .greeting {
            @include flex(column, flex-start, flex-start, nowrap);
            span {
                font-size: 16px;
                @include text($titleFontFamily, 20px, 1.2);
                &:nth-child(2) {
                    //@include text($titleFontFamily, 50px, 1.2);
                    letter-spacing: 5px;
                    margin: 10px 0;
                    span {
                        @include text($titleFontFamily, 22px, 1.2);
                        letter-spacing: 5px;
                        position: relative;
                        z-index: 0;
                        display: inline-block;
                        &:before {
                            content: '';
                            position: absolute;
                            width: 100%;
                            height: 20px;
                            background-color: $primaryColor;
                            bottom: 0;
                            z-index: -1;
                        }
                    }
                }
            }
            h1 {
                @include text($titleFontFamily, 16px, 1.2 );
                margin: 0;
            }
            @media screen and (max-width:1143px ) {
                margin: 0;
            }
        }

        .photo {
            position: relative;
            z-index: 1;
            @media screen and (max-width: 1143px){
                margin-top: 50px;
            }
            .image {
                height: 400px;
                width: 300px;
                @include bgSettings();
                background-position: 80% 0;
                @media screen and (max-width: 600px){
                    height: 360px;
                    width: 260px;
                    transition: .5s;
                }
                &:before {
                    content: '';
                    position: absolute;
                    height: 110%;
                    width: 105%;
                    border: 5px solid $primaryColor;
                    left: 15px;
                    top: -22px;
                    z-index: -1;
                }
            }
        }
        }

}





